export default {
	data() {
		return {
			title: 'Hello',
			form: {
				address: '',
				verify: '',
				newPwd: ''
			}
		}
	},
	onLoad() {

	},
	methods: {
		// 发送验证码
		async sendVerify(){
			if (typeof this.form.address == 'undefined' || this.form.address == ''){
				uni.showToast({
					icon: none,
					title: '请填写注册时的邮箱地址'
				})
				return;
			}
			let ret = await this.$http({
				url: '/sendVerify',
				data: {address: this.form.address}
			})
			if (ret.code == 0) uni.showToast({title: ret.message})
		},
		// 更改密码
		async changePwd(){
			let ret = await this.$http({
				url: '/forget',
				method: 'POST',
				data: this.form
			})
			if (ret.code == 0){
				uni.showToast({title: ret.message})
				uni.navigateTo({
					url: '/pages/login/login'
				})
			}
		}
	}
}
